.banner {
    background: $banner-main;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    position: relative;

    h1 {
        margin: 0;
        text-align: right;
        font-family: $ff-bebas-neue;
        font-size: 109px;
        line-height: 109px;
        text-align: right;
        letter-spacing: 0.03em;

        color: $color-default-white;
        text-transform: uppercase;

        @include vp-1199 {
            font-size: 86px;
            line-height: 86px;
        }

        @include vp-767 {
            font-size: 78px;
            line-height: 78px;
        }
    }
}

.banner__top {
    width: 100%;
    position: relative;
    z-index: 1;
}

.banner__top-container {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
}

.banner__logo {
    margin-top: 30px;
    font-family: $ff-bebas-neue;
    font-size: 26px;
    line-height: 26px;
    letter-spacing: 0.02em;

    color: $color-default-white;

    @include vp-1199 {
        font-size: 24px;
        line-height: 24px;
        margin-top: 39px;
    }

    @include vp-767 {
        font-size: 20px;
        line-height: 20px;
        margin-top: 19px;
    }
}

.banner__tel {
    margin-top: 30px;
    font-family: $ff-bebas-neue;
    font-size: 26px;
    line-height: 26px;
    letter-spacing: 0.03em;

    color: $color-default-white;

    @include vp-1199 {
        font-size: 24px;
        line-height: 24px;
        margin-top: 39px;
        letter-spacing: 0.02em;
    }

    @include vp-767 {
        font-size: 20px;
        line-height: 20px;
        margin-top: 20px;
    }

    &:hover,
    &:focus {
      opacity: 0.8;
    }
}

.banner__container {
    position: relative;
    top: -5px;
}

.banner__block {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: flex-start;
    margin-bottom: 46px;

    @include vp-1199 {
        margin-top: 24px;
        margin-bottom: 77px;
    }

    @include vp-767 {
        margin-top: 76px;
        margin-bottom: 73px;
    }
}

.banner__image {
    @include vp-767 {
        display: none;
    }
}

.banner__description {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
    margin-right: 64px;
    margin-left: 100px;
    
    @include vp-1199 {
        margin-right: 28px;
        margin-left: 92px;
    }

    @include vp-767 {
        margin: auto;
        width: 100%;
    }
}

.banner__title-text {
    position: relative;
    margin-bottom: 55px;

    @include vp-1199 {
        margin-bottom: 40px;
    }

    @include vp-767 {
        margin-bottom: 43px;
    }
}

.banner__title-list {
    position: absolute;
    list-style: none;
    display: flex;
    flex-direction: column;
    text-align: right;
    padding: 0;
    width: 150px;
    bottom: 15px;
    left: -118px;

    @include vp-1199 {
        bottom: 1px;
        left: -128px;
    }

    @include vp-767 {
        bottom: -4px;
        left: -116px;
    }
}

.banner__title-item {
    font-family: $ff-roboto-condensed;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    color: $color-default-white;
}

.banner__subtitle {
    font-family: $ff-bebas-neue;
    font-weight: 400;
    font-style: normal;
    font-size: 49px;
    line-height: 59px;
    letter-spacing: 0.03em;

    color: $color-default-white;
    margin: 0 0 5px 0;
    text-align: right;
    text-transform: uppercase;

    @include vp-1199 {
        font-size: 34px;
        line-height: 41px;
        margin: 0 0 3px 0;
    }

    @include vp-767 {
        font-size: 28px;
        line-height: 34px;
        margin: 0 0 7px 0;
    }
}

.banner__btn {
    margin-bottom: 43px;
    width: 235px;
    min-height: 60px;
    font-size: 12px;
    line-height: 14px;

    @include vp-1199 {
        margin-bottom: 22px;
        width: 187px;
        min-height: 62px;
    }

    @include vp-767 {
        width: 100%;
        margin-bottom: 0;
    }
}

.banner__social-list {
    margin-left: auto;
    margin-bottom: 38px;
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 9px;

    @include vp-1199 {
        display: none;
    }
}

.banner__social-item {
    text-align: right;
}

.banner__social-link {
    color: $color-default-white;
    font-family: $ff-roboto-condensed;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    text-transform: uppercase;

    &:hover,
    &:focus {
      opacity: 0.8;
    }
}